.location {
    border: 4px solid #333;
    border-top: none;

    >figure {
        >.inner {
            .pic {
                background: url(../../../public/img/subImg5.jpg) no-repeat 50% 40%/cover;
            }
        }
    }
   
    >section {
        .contact {
            .inner {
                width: 1180px;
                margin: 0 auto;
                position: relative;
                display: flex;

                .info {
                    width: 50%;
                    padding: 100px 80px 50px;
                    border-left: 2px solid #888;
                    border-right: 2px solid #888;
                    
                    h1 {
                        font: 500 40px/1 'Jost';
                        margin-bottom: 20px;
                    }
    
                    >p {
                        margin-bottom: 50px;
                        font: 20px/1.3 'Jost';
                    }
    
                    article {
                        float: left;
                        width: 49%;
                        margin-right: 2%;
                        margin-bottom: 40px;
    
                        &:nth-of-type(even) {
                            margin-right: 0;
                        }
                        
                        h3 {
                            margin-bottom: 10px;
                            font: 20px/1 'Jost';
                        }
    
                        dt, dd {
                            display: block;
                        }
    
                        dd {
                            padding-left: 10px;
                        }
    
                        p {
                            font: 16px/1.2 'Jost';
                        }
                    }
                }
    
                .form {
                    width: 50%;
                    padding: 100px 100px 50px;
                    border-right: 1px solid #333;
                    text-align: right;
    
                    legend {
                        text-align: center;
                        font: 500 40px/1 'Jost';
                        margin-bottom: 20px;
                        color: #636a52;
                    }
    
                    p {
                        margin-bottom: 50px;
                        font: 20px/1.3 'Jost';
                        text-align: center;
                    }
    
                    input[type="text"],
                    input[type="email"] {
                        display: block;
                        width: 100%;
                        padding: 15px 0;
                        margin-bottom: 30px;
                        border: none;
                        border-bottom: 1px solid #ccc;
    
                        &::placeholder {
                            font: 18px/1 'Inria Sans';
                        }
                    }
    
                    button {
                        position: relative;
                        display: inline-block;
                        width: 75px;
                        height: 75px;
                        background: #636a52;
                        font: 18px/1 'Inria Sans';
                        color: #fff;
                        border: 1px solid #333;
    
                        &::before {
                            content: '';
                            position: absolute;
                            top: -7px;
                            left: -7px;
                            width: 75px;
                            height: 75px;
                            background: #828b6b;
                            z-index: -1;
                            border: 1px solid #333;
                        }
                        
                    }
                }
            } 
            
            
        }

        .store {
            padding: 50px 0;
            border-top: 4px solid #333;
            .inner {
                width: 1180px;
                margin: 0 auto;
                position: relative;

                &::after {
                    content: '';
                    display: block;
                    clear: both;
                }

                h2 {
                    float: left;
                    margin: 10px;
                    font: 500 40px/1 'Jost';
                }

                .branch {
                    float: right;

                    button {
                        padding: 10px;
                        margin: 5px 0 0 30px;
                        border: 1px solid #333;
                        font: 20px/1 'Inria Sans';
                    }
                }

                .traffic {
                    padding-top: 20px;
                    float: right;

                    button {
                        padding: 10px;
                        margin: 5px 0 0 30px;
                        border: 1px solid #333;
                        font: 20px/1 'Inria Sans';
                    }
                }
            }
            
            

            #map {
                width: 100%;
                height: 500px;
                background: #000;
                filter: grayscale(1);
            }
        }
        
    }
}

@media screen and (max-width: 1179px){
    .location {
       
        >section {
            .contact {
                .inner {
                    width: 100%;
                    .info {
                        border-left: none;

                        article {
                            
                            &:first-of-type {
                                h3 {
                                    margin-bottom: 32px;
                                }
                            }
                        }
                    }
                } 
            }
    
            .store {
                .inner {
                    width: 100%;
                    padding: 0 15px;
                }
            }
            
        }
    }
}

